<template>
<span>


      <v-dialog v-model="store.openFilter" persistent>
      <v-card>
      <v-card-title>Фильтровка и сортировка</v-card-title>
      <div style="height:60vh;maxheight:60vh;overflow:auto;padding=40px">
        <v-simple-table v-if="!load" dense light>
          <template v-slot:default>
            <tbody>
              <tr v-for="(column, index) in OpenMapData().Fcols" :key="column.FieldName" style="background-color:white;" >
                <td style="border-bottom: none;">
                  <v-text-field :label="column.FieldCaption" v-model="column.FindString"
                      :append-icon="getIcon(column)"
                      @click:append="sortChangeIndex(column, index)"
                  ></v-text-field>
                </td>
                <!--
                <td style="border-bottom: none;width:100px">
                  <v-select
                    :items="items"
                    v-model="column.Sort"
                    @change="(event)=>sortChange(event, index)"
                  ></v-select>
                </td>
                -->

                <td style="border-bottom: none;width:50px">
                  {{column.SortOrder}}
                  <span hidden>{{rangSort}}</span>
                  <span hidden>{{nactord}}</span>
                  
                </td>

              </tr>
            </tbody>
          </template>
        </v-simple-table>
      
      
    </div>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="updateTab()">ОК</v-btn>
          <v-btn color="green darken-1" text @click="store.openFilter = false">Отмена</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-toolbar flat>
        <v-toolbar-title>Справочник контактов</v-toolbar-title>
        <v-toolbar-title v-if="load">(загрузка...)</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-spacer></v-spacer>
        <v-spacer></v-spacer>
        <v-text-field v-if="!load" 
            label="Поиск"
            dense
            filled
            rounded
            hide-details
            prepend-inner-icon="mdi-magnify"
            single-line
            v-model="OpenMapData().Fcols[3].FindString"
            @input="updateTab()"
          ></v-text-field>
    </v-toolbar>    
    
       
       <v-simple-table v-if="!load" light>
          <template v-slot:default>
            
            <tbody>
              <tr 
                v-for="(row, index) in OpenMapData().MainTab"
                :key="index"
              >
                <td>
                    <v-list-item-avatar>
                        <v-img :src="avatar(index)"></v-img>
                    </v-list-item-avatar>
                </td>
                <td>
                        <v-card
                            class="mx-auto"
                            tile
                            flat
                            max-width="400"
                        >
                        <v-list-item  dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["Должность"]}}</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["ПолнИмяСотрудника"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item  dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>Табельный номер</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["ТабN"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        </v-card>
                </td>    

                <td>
                        <v-card
                            class="mx-auto"
                            tile
                            flat
                            max-width="300"
                            
                        >
                        <v-list-item dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>Компания</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["Организация"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item dense>
                            <v-list-item-content>
                                <v-list-item-subtitle>Подразделение</v-list-item-subtitle>
                                <v-list-item-title class="caption">{{row["Подразделение"]}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>

                        </v-card>
                </td>


                <td>
                        <v-card
                            class="mx-auto"
                            tile
                            flat
                            max-width="300"
                        >
                        <v-list-item dense>
                            <v-list-item-icon>
                                <v-icon>mdi-home</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["ОфисСотрудника"]}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>

                        <v-list-item dense>
                            <v-list-item-icon>
                                <v-icon>mdi-phone</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["CorpMobile"]}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>


                        <v-list-item  dense>
                            <v-list-item-icon>
                                <v-icon>mdi-email-outline</v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-subtitle>{{row["Email"]}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>


                        </v-card>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <Pagination :findData="OpenMapData()" v-if="!load" />
</span>
</template>
<script>
  import { useMainStore } from '../store/index'
  import { prodaction, baseUrl } from "../main";
  import Pagination from "./PaginationTool";
  export default {
    name: 'UserPage',
    components: { Pagination },
    setup() {
      
      //let elHtml = document.getElementsByTagName("html")[0];
      //elHtml.style.overflowY = "hidden";  

      const store = useMainStore()
      
      return { store }
    },
    data: () => ({
                load: true,
                tableData: {},
                updating: false,
                action: 1,
                stateDrawer: false,
                rangSort: 0,
                nactord: 1
          }),
    methods:
    {
        sortChange: function(event, index) {
        let rang = 0;
        let columns = this.OpenMapData().Fcols;
        columns.map((column, i) => {
            if (column.Sort == "Нет")  
            column.SortOrder = null;

            if (i != index && column.SortOrder)
            if (column.SortOrder > rang) rang = column.SortOrder;
            
        });
        columns[index].SortOrder = rang + 1;
        this.rangSort = rang + 1;
        this.nactord = this.nactord + 1;
        //columns[index].Sort = event.target.value;
        },

        sortChangeIndex: function(column, index)
        {
        if (column.Sort == "ASC")
            column.Sort = "DESC"
        else
        if (column.Sort == "DESC")    
            column.Sort = "Нет"
        else
            column.Sort = "ASC"

        this.sortChange (null, index)  
        },

        getIcon: function(column)
        {
            if (column.Sort == "ASC")
                return "mdi-arrow-down"
        
            if (column.Sort == "DESC") 
                return "mdi-arrow-up"
        
            return "mdi-sort";
        },

        OpenMapData: function() {
            return this.tableData;
        },
        avatar: function(index) {
            return "https://randomuser.me/api/portraits/women/" + index.toString() + ".jpg";    
        },
        dateformat: function(d, f) {
            if (!d) return d;

            if (d.length != 24) {
                let res = f.match(/0\.(0+)/);

                let n = 0;
                if (res)
                if (res.length > 1) {
                    n = res[1].length;
                }

                if (n > 0) return Number(d.toString()).toFixed(n);
                else return d;
            }
            f = f.replace("yyyy", d.substr(0, 4));
            f = f.replace("yy", d.substr(2, 2));
            f = f.replace("MM", d.substr(5, 2));
            f = f.replace("dd", d.substr(8, 2));
            f = f.replace("HH", d.substr(11, 2));
            f = f.replace("mm", d.substr(14, 2));
            return f;
        },
        
        onChangePage: function(p) {
            this.action = this.action + 1;
            this.OpenMapData().page = p + 1;
            this.updateTab();
        },
        
        updateTab: async function() {

            const url = baseUrl + "React/FinderStart";
            let bd = new FormData();
            let mid = this.OpenMapData();
            bd.append("id", 1467);
            bd.append("mode", "data");
            bd.append("page", mid.page.toString());
            bd.append("Fc", JSON.stringify(mid.Fcols));
            
            if (mid.SQLParams) bd.append("SQLParams", JSON.stringify(mid.SQLParams));

            if (mid.TextParams)
                bd.append("TextParams", JSON.stringify(mid.TextParams));
                
            const response = await fetch(url, {
                method: "POST",
                mode: prodaction ? "no-cors" : "cors",
                cache: "no-cache",
                credentials: prodaction ? "include" : "omit",
                body: bd
            });

            const data = await response.json();
            if (data.Error) {
                alert(data.Error);
            } else {
                mid.MainTab = data.MainTab;
                mid.TotalTab = data.TotalTab;
                mid.page = data.page;
                this.store.openFilter = false;
            }
            

        }    
    },
    computed:{
        gridHeight: function()
            {
                return document.documentElement.clientHeight - 120;
            },
    },
    mounted: async function() 
    {

      const url = baseUrl + "React/FinderStart";
      let bd = new FormData();
      bd.append("id", 1467);
      bd.append("mode", "data");
      const response = await fetch(url, {
          method: "POST",
          mode: prodaction ? "no-cors" : "cors",
          cache: "no-cache",
          credentials: prodaction ? "include" : "omit",
          body: bd
      });
      
      this.tableData = await response.json();
      
      
      if (this.tableData.Error) 
      {
         alert(this.tableData.Error);
      } 
      else 
      {
        
        const menu = [
      
              { text: "Фильтры", icon: "mdi-filter-menu" , action: () =>
                    {
                        this.store.openFilter = true;
                    }
              },
              /*
              { text: "Страницы", icon: "mdi-code-tags" , action: ()=>
                    {
                        this.stateDrawer = true;
                    }
              }
              */
            ];
              this.store.addItem(menu)
        this.load = false;
      }
        
    }      
}
</script>